import Dialog from '@/wxcomponents/dist/dialog/dialog';
import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			taskId: '',
			allWordCount: '',
			taskTime: '',
			className:'',
			taskDetailsLists: [],
			show1: false,
			show: true
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 查看详情跳转
		linkTo(studentId) {
			uni.navigateTo({
				url: 'studentDetails?studentId=' + studentId
			})
		},
		// 查看排名跳转
		linkToRanking() {
			uni.navigateTo({
				url: 'ranking?taskId=' + this.taskId + '&allWordCount=' + this.allWordCount
			})
		},
		// 任务详情列表
		taskDetailsList(taskId) {
			console.log(taskId)
			this.$minApi.taskDetailsList({
				taskId: taskId
			}, ).then(data => {
				console.log(data)
				this.taskDetailsLists = data.data

				console.log(this.taskDetailsLists)
			}).catch(err => {
				// console.log(err)
			})
		},
		// 删除任务事件
		delectTask() {
			Dialog.confirm({
				title: '  ',
				message: '是否确定删除任务？'
			}).then(() => {
				console.log("确认")
				Toast("删除成功");
				// on confirm
			}).catch(() => {
				// on cancel
				console.log("取消")
			});
		},

	},
	onLoad(options) {
		// var data = JSON.parse(options.index); // 字符串转对象
		console.log(options)
		this.taskId = options.taskId
		this.taskTime = options.taskTime
		this.allWordCount = options.allWordCount
		this.className=options.className

		this.taskDetailsList(options.taskId)

	}
}
